const descriptionText = `Fundacja Krzewienia Kultury i Turystyki Nad Rzek zobowizuje si zapewni dostpno swojej strony internetowej zgodnie z ustaw z dnia 4 kwietnia 2019 r. o dostpnoci cyfrowej stron internetowych i aplikacji mobilnych podmiotw publicznych. Owiadczenie w sprawie dostpnoci ma zastosowanie do serwisu internetowego www.fundacjanadrzeka.com <br/> <p></p>
                    Data publikacji strony internetowej: 31 sierpnia 2009 r. Data ostatniej duej aktualizacji: 25 listopada 2023 r. <br/> <p></p>
                                         Strona internetowa jest czciowo zgodna z ustaw z dnia 4 kwietnia 2019 r. o dostpnoci cyfrowej stron internetowych i aplikacji mobilnych Fundacji tj. z wyczeniem niektrych filmw i zdj ktre nie posiadaj napisw dla osb niesyszcych i sabosyszcych. <br/> <p></p>
                                         Deklaracj sporzdzono 20231225 na podstawie samooceny przeprowadzonej przez zarzd Fundacji. <br/> <p></p>
                    W przypadku problemw z dostpnoci strony internetowej prosimy o kontakt z p. Imi Nazwisko, za porednictwem poczty elektronicznej: Email. T sam drog mona skada wnioski o udostpnienie informacji niedostpnej oraz skada skargi na brak zapewnienia dostpnoci. <br/> <p></p>
                    Kady ma prawo do wystpienia z daniem zapewnienia dostpnoci cyfrowej strony internetowej, aplikacji mobilnej lub jakiego ich elementu. Mona take zada udostpnienia informacji za pomoc alternatywnego sposobu dostpu, na przykad przez odczytanie niedostpnego cyfrowo dokumentu, opisanie zawartoci filmu bez audiodeskrypcji itp. danie powinno zawiera dane osoby zgaszajcej danie, wskazanie, o ktr dokadnie stron internetow chodzi oraz sposb kontaktu. Jeeli osoba dajca zgasza potrzeb otrzymania informacji za pomoc alternatywnego sposobu dostpu, powinna take okreli dogodny dla niej sposb przedstawienia tej informacji. Fundacja powinna zrealizowa danie niezwocznie, nie pniej ni w cigu 7 dni od dnia wystpienia z daniem. <br/> <p></p>
                    Jeeli dotrzymanie tego terminu nie jest moliwe, Fundacja niezwocznie informuje o tym wnoszcego danie, kiedy realizacja dania bdzie moliwa, przy czym termin ten nie moe by duszy ni 2 miesice od dnia wystpienia z daniem. <br/> <p></p>
                    Jeeli zapewnienie dostpnoci cyfrowej nie jest moliwe, Fundacja moe zaproponowa alternatywny sposb dostpu do informacji. W przypadku, gdy Fundacja odmwi realizacji dania zapewnienia dostpnoci lub alternatywnego sposobu dostpu do informacji, wnoszcy danie moe zoy skarg w sprawie zapewnienia dostpnoci cyfrowej strony internetowej, aplikacji mobilnej lub elementu strony internetowej, lub aplikacji mobilnej. <br/> <p></p>
                    Po wyczerpaniu wskazanej wyej procedury mona take zoy wniosek do Rzecznika Praw Obywatelskich. <br/> <p></p>
                    Na stronie internetowej mona korzysta ze standardowych skrtw klawiaturowych. <br/>
                    Uytkownicy korzystajcy wycznie z klawiatury mog swobodnie porusza si po serwisie za pomoc klawisza Tab. <br/> <p></p>
                    Uytkownicy maj moliwo powikszania treci w serwisie  powikszanie czcionki A+, A++. Istnieje rwnie moliwo wczenia kontrastu serwisu. <br/> <p></p>`;


