const coRobimyText = `<p>Turystyka BEZ FILTRW  to marka, ktra startowaa jako hobbystyczny blog turystyczny, z czasem rozrastajc si do znaczcej w skali Bydgoszczy i regionu, inicjatywy. Dzi obejmuje ona stron internetow (www.turystykabezfiltrow.com), na ktrej regularnie pojawiaj si artykuy o tematyce turystyczno-kulturowej (m.in. cykle: Bydgoskie osiedla BEZ FILTRW, Bydgoszcz przez dziurk od klucza), kanay na portalu YouTube (Turystyka BEZ FILTRW oraz RBF  Rozmowy BEZ FILTRW), a take cykliczne spacery tematyczne po Bydgoszczy, gwnie za po bydgoskich osiedlach (w ramach serii Bydgoskie osiedla BEZ TAJEMNIC).</p>
<p>Przeczytawszy powyszy tekst, nietrudno si domyli, e Turystyka BEZ FILTRW celuje w propagowanie turystyki osiedlowej. Inicjatywa Osiedla BEZ FILTRW, bya dotychczas wdraana (przy wsparciu rodkw finansowych lokalnych samorzdw), w: Bydgoszczy, Toruniu, Wocawku, Grudzidzu i Inowrocawiu.</p>
<p><strong>Dlaczego BEZ FILTRW?</strong></p>
BEZ FILTRW czyli bez upikszaczy, podkrcania rzeczywistoci, tak powszechnego w dobie wszechobecnych mediw spoecznociowych. Nasze materiay ukazuj pikno (lub brzydot) danego miejsca oraz jego zasoby, w taki sposb, aby najpeniej odda rzeczywisto. Jakkolwiek zaskakujca lub nawet niewygodna, ona jest. 
`;
const piotrDescription = `<p>Pomysodawca inicjatywy, autor tekstw i scenariuszy filmw, ktre pojawiaj si na blogu oraz kanale YouTube. Przewodnik turystyczny, specjalizujcy si w historii i wspczesnoci bydgoskich osiedli (w 2023 roku poprowadzi ponad 60 wycieczek).</p>
<p>Prezes Fundacji Krzewienia Kultury i Turystyki Nad Rzek, specjalista ds. pozyskiwania rodkw zewntrznych (od 2021 roku prowadzi firm Pisanie&Projekty), czonek Konwentu Kolegium Nauk Spoecznych Wyszej Szkoy Gospodarki w Inowrocawiu.  Ponadto instruktor turystyki kwalifikowanej o specjalizacji turystyka kajakowa.</p>
<p>W 2023 r. za dziaania podejmowane w ramach inicjatywy Turystyka BEZ FILTRW, otrzyma nagrod Marszaka Wojewdztwa Kujawsko-Pomorskiego w kategorii Promocja turystyczna. </p>
<p>Absolwent Uniwersytetu Kazimierza Wielkiego w Bydgoszczy i Uniwersytetu Mikoaja Kopernika w Toruniu.</p>
Bydgoszczanin z dziada pradziada (4 pokolenie), mionik miasta i regionu, a take Kresw Wschodnich. 
`;
const innaDescription = `<p>Pasjonatka fotografii, odpowiadajca za dokumentacj zdjciow wszystkich wydarze organizowanych przez Turystyk BEZ FILTRW, a take artykuw, publikowanych na blogu. Operatorka kamery oraz montaystka  autorka filmw, pojawiajcych si na kanale YouTube Turystyka BEZ FILTRW.</p>
<p>Laureatka w konkursie pn. Nagroda Prezydenta Miasta Bydgoszczy, na najlepsze prace magisterskiego w roku akademickim 2018/2019, za prac pt. Ksztatowanie wizerunku turystycznego miasta oraz jego postrzeganie przez studentw zagranicznych na przykadzie Bydgoszczy.</p>
Ukrainka pochodzca ze wschodniego skraju Podola, mieszkajca w Bydgoszczy od 2014 roku.`;

